html {
  background-color: #007a7a;
  background-image: url("https://www.transparenttextures.com/patterns/inspiration-geometry.png");
  font-family: "helvetica neue";
  text-align: center;
}

.App {
  max-width: 400px;
  margin: 50px auto;
  background: #f7f0e9;
  color: #007a7a;
  border-radius: 5px;
  box-shadow: 5px 5px 15px -5px rgba(0, 0, 0, 0.3);
}

h1,
h3 {
  color: #007a7a;
  padding: 10px;
  text-align: center;
}

h3 {
  padding-top: 0px;
}

.box {
  max-width: 400px;
  margin: 50px auto;
  background: #f7f0e9;
  border-radius: 5px;
  box-shadow: 5px 5px 15px -5px rgba(0, 0, 0, 0.3);
}

.item {
  min-height: 70px;
  display: flex;
  align-items: center;
  border-bottom: 1px solid #f1f1f1;
}

.item:last-child {
  border-bottom: 0;
}

input:checked + p {
  text-decoration: line-through;
  text-decoration-color: #007a7a;
}

input[type="checkbox"] {
  margin: 20px;
}

p {
  margin: 0;
  padding: 20px;
  font-size: 20px;
  font-weight: 600;
  color: #007a7a;
}

form {
  text-align: center;
  margin-left: 20px;
}

button {
  min-height: 50px;
  width: 50px;
  border-radius: 50%;
  border-color: transparent;
  background-color: #007a7a;
  color: #fff;
  font-size: 30px;
  padding-bottom: 0px;
  border-width: 0;
  cursor: pointer;
}

input[type="text"] {
  text-align: center;
  height: 60px;
  top: 10px;
  border: none;
  background: transparent;
  font-size: 20px;
  font-weight: 200;
  width: 313px;
}

input[type="text"]:focus {
  outline: none;
  box-shadow: inset 0 -3px 0 0 #007a7a;
}

::placeholder {
  color: grey;
  opacity: 1;
}

footer {
  color: white;
  color: rgba(0, 0, 0, 0.5);
  text-align: center;
}
